@import url(https://fonts.googleapis.com/css2?family=Carrois+Gothic+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body{font-family:'Carrois Gothic SC', Roboto, sans-serif;margin:0}a{text-decoration:none}

@-webkit-keyframes slideIn{from{left:-100%}to{left:0}}@keyframes slideIn{from{left:-100%}to{left:0}}@-webkit-keyframes slideOut{from{left:0}to{left:-100%}}@keyframes slideOut{from{left:0}to{left:-100%}}#sidebar{-webkit-animation-duration:0.5s;animation-duration:0.5s;border-right:1px solid gray;background-color:white;color:black;height:100%;padding:20px;position:absolute;top:0;left:0}#sidebar header h1,#sidebar header i{display:inline;padding:0 20px;vertical-align:middle}#sidebar .category{padding-top:10px;text-align:left}#sidebar .category i,#sidebar .category p{display:inline;vertical-align:middle}#sidebar .category ul{padding-left:40px}#sidebar .category li{list-style:none;padding:5px 0}

nav{background-color:lightgrey;padding:20px;text-align:center}nav a{color:black}nav .menu{float:left}nav .home{font-size:30px}nav .login{float:right}nav .menu,nav .login{padding:5px}@media only screen and (max-height: 700px){nav .home{font-size:20px}}


form{display:flex;flex-direction:column;justify-content:center;align-items:center;height:85vh;font-size:18px}form label::after{color:red;content:' *'}@media only screen and (min-width: 500px){form .border{padding:100px}form input{width:20em;height:1.75em}}form .border{border:1px solid black;border-radius:10px;padding:30px}form input{display:block;font-family:'Segoe UI', 'Roboto', sans-serif;font-size:1em;margin:10px 0;padding:1px 3px}form .submit{text-align:center}form .submit #login,form .submit #createAccount{padding:10px 30px;margin:10px 0}form .submit #google-login,form .submit #create-google-login{color:white;background-color:#4285F4;border:1px solid black;border-radius:3px;font-family:"Montserrat", sans-serif;font-weight:600;padding:10px 20px}.inputField{padding:10px 0}@media only screen and (min-width: 500px){.inputField a:nth-child(1){float:left}.inputField a:nth-child(2){float:right}}

